import React, { useRef, useEffect } from 'react';
import Search from "antd/es/input/Search";
import {Select} from "antd";

const MyComponent = () => {
    const inputRef = useRef(null);

    useEffect(() => {
        if (inputRef.current) {
            inputRef.current.focus();
        }
    }, []);

    return (
        <div style={{width: '100%'}}>
            <Select ref={inputRef} showSearch size="large" placeholder="Type a movie title" allowClear style={{width: '80%'}} suffixIcon={null}/>
            {/*<Search ref={inputRef} placeholder="Auto-focus on page load!!" enterButton="Search" size="large" loading/>*/}
        </div>
    );
};

export default MyComponent;
